'use client'

import {useState} from 'react'
import CIDR from 'ip-cidr'

function calculateCidrDetails(cidrInput: string) {
    try {
        return new CIDR(cidrInput)
    } catch (e) {
        return null // Handle invalid CIDR
    }
}

/**
 * https://github.com/ortexx/ip-cidr
 * https://mxtoolbox.com/subnetcalculator.aspx
 */
export default function KrIp() {
    const [cidrInput, setCidrInput] = useState<string>('1.0.0.14/12');
    const [calculationResult, setCalculationResult] = useState<any>(null);

    const handleCalculateButtonClick = () => {
        const result = calculateCidrDetails(cidrInput);
        setCalculationResult(result);
    };

    return (
        <div>
            <h1>Subnet Calculator</h1>
            <input
                type="text"
                placeholder="Enter CIDR (e.g,. 1.0.0.14/12)"
                value={cidrInput}
                onChange={(e) => setCidrInput(e.target.value)}
            />
            <button onClick={handleCalculateButtonClick}>Calculate</button>

            {calculationResult && (
                <div style={{marginTop: '20px'}}>
                    <h2>Calculation Results</h2>
                    <table style={{width: '100%', backgroundColor: '#f0f0f0'}}>
                        <tbody>
                        <tr>
                            <td><strong>Input:</strong></td>
                            <td>{calculationResult.cidr}</td>
                        </tr>
                        <tr>
                            <td><strong>CIDR Range:</strong></td>
                            <td>{calculationResult.start() + ' - ' + calculationResult.end()}</td>
                        </tr>
                        <tr>
                            <td><strong>Input IP (Long):</strong></td>
                            <td>{calculationResult.address.bigInteger()[0]}</td>
                        </tr>
                        <tr>
                            <td><strong>CIDR Range (Long):</strong></td>
                            <td>{calculationResult.start({type: 'bigInteger'}) + ' - ' + calculationResult.end({type: 'bigInteger'})}</td>
                        </tr>
                        <tr>
                            <td><strong>Input IP (Hex):</strong></td>
                            <td>{calculationResult.address.toHex()}</td>
                        </tr>
                        <tr>
                            <td><strong>CIDR Range (Hex):</strong></td>
                            <td>{calculationResult.addressStart.toHex() + ' - ' + calculationResult.addressEnd.toHex()}</td>
                        </tr>
                        </tbody>
                    </table>
                </div>
            )}
        </div>
    )
}